import { makeStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import React from 'react';
import ReactButton from '../../CustomComponents/ReactButton/ReactButton'
import AddIcon from '@material-ui/icons/Add';
import '../../App.css'
//styles for enhanced table toolbar
const useToolbarStyles = makeStyles((theme) => ({
    root: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(1),
      display:'flex',
      justifyContent:'space-between',
      alignItems:'center',
      padding:'0 30px'
    },
    title: {
      flex: '1 1 100%',
    },
  }));
  
  //thiis is to show "Agent" at top Helps in fixing 
  //them just above table
  const EnhancedTableToolbar = () => {
    const classes = useToolbarStyles();
  
    return (
      <Toolbar
        className={classes.root}
      >
       <Typography variant="h5">Agents</Typography>
<ReactButton width={"140px"} className="flex">Add Agent <AddIcon style={{fontSize:'25px',marginTop:'4px'}}/></ReactButton>
      </Toolbar>
    );
  };

  export default EnhancedTableToolbar;